import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	data() {
		return {
			coupon:null,
			show: false,
			type: [],
			list: [],
			server: null
		};
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	components: { uniPopup },
	onLoad(opt) {
		this.getServer();
		if(opt.coupon){
			console.log(opt.coupon)
			this.coupon=JSON.parse(opt.coupon);
		}
		this.getServerType();
		uni.$on("choose",data=>{
			this.coupon=data;
		})
	},
	methods: {
		getServer() {
			this.$Fetch({
				url: 'serve_people/getDataById',
				success: res => {
					if (res) this.server = res;
				}
			});
		},
		getServerType() {
			this.$Fetch({
				url: 'Qualifications/getData',
				data: { pageNum: 1, pageSize: 10 },
				success: res => {
					let arr = res.lists;
					if (arr.length > 0) {
						this.list = arr;
					}
				}
			});
		},
		checkType(type) {
			let i = this.type.indexOf(type);
			if(i>-1){
				this.type.splice(i,1)
			}else{
				this.type.push(type)
			}
		},
		onSubmit(e) {
			let data=e.detail.value;
			data.qualifications=this.type;
			if(this.coupon){
				data.user_coupon_id=this.coupon.id;
			}
			
			console.log('提交数据：', data)
			this.$Fetch({
				url: 'Order/add',
				data: data,
				debug:true,
				success: res => {
					this.show=false;
					uni.showToast({
						title:"下单成功",
						icon:"success"
					})
					setTimeout(()=>{
						uni.redirectTo({
							url:"/pages/ucenter/order/order"
						})
					},1500)
				}
			});
		}
	}
};
